body {
  margin: 0;
  padding: 0;
}

.header {
  --linkcolor: #00acdc;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--linkcolor);
}

.is-active {
  color: var(--linkcolor);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--linkcolor);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

